// HK Grotesk
@font-face {
  font-family: 'HK Grotesk';
  src: local('HK Grotesk Light Web'), 
      local('HKGrotesk-LightWeb'),
      url('../../fonts/hkgrotesk/hkgrotesk-light-web.woff2') format('woff2'),
      url('../../fonts/hkgrotesk/hkgrotesk-light-web.woff') format('woff');
  font-weight: 300;
  font-style: normal;
}

@font-face {
  font-family: 'HK Grotesk';
  src: local('HK Grotesk Regular Web'), local('HKGrotesk-RegularWeb'),
      url('../../fonts/hkgrotesk/hkgrotesk-regular-web.woff2') format('woff2'),
      url('../../fonts/hkgrotesk/hkgrotesk-regular-web.woff') format('woff');
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'HK Grotesk';
  src: local('HK Grotesk Medium Web'), 
      local('HKGrotesk-MediumWeb'),
      url('../../fonts/hkgrotesk/hkgrotesk-medium-web.woff2') format('woff2'),
      url('../../fonts/hkgrotesk/hkgrotesk-medium-web.woff') format('woff');
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: 'HK Grotesk';
  src: local('HK Grotesk Bold Web'), 
      local('HKGrotesk-BoldWeb'),
      url('../../fonts/hkgrotesk/hkgrotesk-bold-web.woff2') format('woff2'),
      url('../../fonts/hkgrotesk/hkgrotesk-bold-web.woff') format('woff');
  font-weight: 700;
  font-style: normal;
}